
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "goodsInfo",
components: {
ViewBox,
XHeader,
Card,
XButton
},
created() {
this.queryProduct();
},
data() {
return {
productInfo: {},
userInfo: {},
price: 0
};
},
methods: {
goGetGoods() {
this.$router.push("/getGoods");
},
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.userInfo = data.data.user
switch(this.userInfo.vipLevel)
{
case 1: // 经销商
this.price = this.productInfo.dealerPrice
break;
case 2: // 总代理
this.price = this.productInfo.proxyPrice
break;
default: // 会员 普通用户
this.price = this.productInfo.vipPrice
}
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryProduct() {
this.$api.queryProduct().then(data => {
if (data.code === 200) {
this.productInfo = data.data.product;
this.queryUser();
} else {
this.$vux.toast.text(data.message, "top");
}
});
}
}
};



